import React from 'react';
import { Layout } from 'antd';
import AppHeader from './components/AppHeader';

const { Content } = Layout;

const content_style = {
    margin: '24px 16px 0',
}

const layout_style = {
    height: '100vh',
    maxHeight: '100vh',
    overflow: 'auto'
}

const BasicLayout = props => {
    return (
        <Layout style={layout_style}>
            <Layout style={layout_style}>
                <AppHeader />
                <Content style={content_style}>
                    <div className="site-layout-background" style={{ padding: 0, minHeight: 360, marginBottom: 60 }}>
                        {props.children}
                    </div>
                </Content>
            </Layout>
        </Layout>
    )
};

export default BasicLayout;