import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { getCookie } from './utils';
import { APP_COOKIE_NAME, AUTHORIZATION } from './constants';

import MainContainer from './containers/MainContainer';
import LoginContainer from './containers/LoginContainer';

import './App.css';
import './assets/css/fontAwesome/fontawesome.css';
import './assets/css/fontAwesome/duotone.css';
import './assets/css/fontAwesome/light.min.css';
import 'bootstrap/dist/css/bootstrap.min.css';

const App = () => {

  const isAuth = useSelector(state => state.authorization.auth);
  const dispatch = useDispatch();

  useEffect(() => {
    //COMPROBAR VALIDEZ TOKEN
    if(getCookie(APP_COOKIE_NAME)){
      dispatch({
        type: AUTHORIZATION,
        payload: { token: getCookie(APP_COOKIE_NAME), auth: true }
      })
    }
  })

  return (
    <div className="App">
      { isAuth
          ? <MainContainer />
          : <LoginContainer />
      }
    </div>
  )
};

export default App;