import React, { Fragment } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import 'moment/locale/es';
import history from '../../history';
import { Row, Col } from 'react-flexbox-grid';
import { Button, Typography, Tag, Progress, Popover, Skeleton, Switch } from 'antd';
import { CalendarOutlined, UserOutlined, CommentOutlined, CheckOutlined, CloseOutlined, EyeOutlined } from '@ant-design/icons';
import PriorityIcon from './../PriorityIcon';
import { changeTaskStatus } from '../../actions/tasksActions';
import { PROGRESS_STROKE_COLOR } from './../../constants';

import './css/index.css';

const { Title } = Typography;

const Task = ({ data, type }) => {
    /* DISPATCHER */
    const dispatch = useDispatch();

    /* LOADING STATE */
    const loading = useSelector(state => state.loading);

    /* CONFIGURACIÓN DE FECHAS */
    var now = moment();
    var start = moment(data.fechaInicio);
    var end = moment(data.fechaVencimiento);
    const taskPercent = () => {
        start = start || moment(now).startOf('day');
        end = end || moment(now).endOf('day');
        let totalMillisInRange = end.valueOf() - start.valueOf();
        let elapsedMillis = now.valueOf() - start.valueOf();
        return Math.round(Math.max(0, Math.min(100, 100 * (elapsedMillis / totalMillisInRange))));
    }

    /* POPOVER OBVERSACIONES */
    const popContentObservations = (
        <div><p>
            {data.observaciones ? data.observaciones : 'Sin observaciones.'}
        </p></div>
    )

    const popContentSupervisor = (
        <div>
            <p>{data.supervisorNombre}</p>
        </div>
    )

    const taskStatus = status => {
        switch (status) {
            case 1:
                return false;

            case 3:
                return true;

            default:
                break;
        }
    }

    const handleStatusChange = (status, oid) => {
        dispatch(changeTaskStatus(status, oid))
    }

    const handleExtendTask = () => {
        history.push(`/tasks/${type}/${data.oid}`)
    }

    return (
        <Fragment>
            <Col xs={12} md={12} lg={6}>
                <div className={`task ${loading ? 'd-none' : ''}`}>
                    {/* TITULO / DESCRIPCIÓN */}
                    <Title strong level={4} className="m-0">
                        <span onClick={() => handleExtendTask()}>
                            {data.descripcion}
                        </span>
                    </Title>
                    {/* RESPONSABLE DE LA TAREA */}
                    <Tag color="cyan">{data.primerResponsableNombre}</Tag>
                    {/* PORCENTAJE PARA TERMINAR */}
                    <Progress percent={taskPercent()} size="small" strokeColor={PROGRESS_STROKE_COLOR} />
                    <Row className="m-0 d-flex justify-content-between align-items-center">
                        <Col>
                            {/* PRIORIDAD DE LA TAREA */}
                            <PriorityIcon level={data.prioridad} />
                        </Col>
                        <Col>
                            {/* FECHA DE INICIO Y FINALIZACIÓN */}
                            <Button shape="round" type="dashed" icon={<CalendarOutlined />}>{start.format('L')} - {end.format('L')}</Button>
                        </Col>
                    </Row>
                    <Row className="mx-0 mt-2 d-flex justify-content-between align-items-center">
                        <Col>
                            {/* VER TAREA EXTENDIDA */}
                            <Button shape="round" type="dashed" icon={<EyeOutlined />} onClick={() => handleExtendTask()} />
                        </Col>
                        <Col>
                            {/* OBSERVACIONES DE LA TAREA */}
                            <Popover content={popContentObservations} title="Observaciones" overlayStyle={{ width: '70%' }}>
                                <Button shape="round" type="dashed" icon={<CommentOutlined />} />
                            </Popover>
                        </Col>
                        <Col>
                            {/* SUPERVISOR DE LA TAREA */}
                            <Popover title="Supervisor" content={popContentSupervisor}>
                                <Button shape="round" type="dashed" icon={<UserOutlined />} />
                            </Popover>
                        </Col>

                        <Col>
                            <Switch defaultChecked={taskStatus(data.estado)} onChange={(e) => handleStatusChange(e, data.oid)} checkedChildren={<CheckOutlined />} unCheckedChildren={<CloseOutlined />} />
                        </Col>
                    </Row>

                    <Row>
                        <Col xs={12}>
                            <Skeleton loading={loading} active paragraph={{ rows: 2 }} />
                        </Col>
                    </Row>
                </div>
            </Col>
        </Fragment>
    )
}

export default Task;