import React from 'react';
import { API_URL, TASKS_SUPERVISED_ENDPOINT, TASKS_OWN_ENDPOINT, TASKS_ENDED_ENDPOINT, PROXY, SET_TASK_RUNNING_ENDPOINT, SET_TASK_END_ENDPOINT } from './../constants/api';
import { FETCH_TASKS, UPDATE_TASK } from './../constants';
import { message } from 'antd';
import Axios from 'axios';

const endpointType = type => {
    switch (type) {
        case 'own':
            return TASKS_OWN_ENDPOINT;
        case 'supervised':
            return TASKS_SUPERVISED_ENDPOINT;
        case 'ended':
            return TASKS_ENDED_ENDPOINT;
        default:
            return '';
    }
}

const changeTaskStatusEndpoint = status => {
    if (status) {
        return SET_TASK_END_ENDPOINT;
    } else {
        return SET_TASK_RUNNING_ENDPOINT;
    }
}

export const fetchTasks = (type, userId) => {
    return dispatch => {

        Axios.get(`${PROXY}${API_URL}${endpointType(type)}${userId}/info`)
            .then(response => {
                if (response.data[0] === null) {
                    dispatch({ type: FETCH_TASKS, payload: [] })
                } else {
                    dispatch({ type: FETCH_TASKS, payload: response.data })
                }
            }).catch(error => console.log(error))
    }
}

export const changeTaskStatus = (taskStatus, oid) => {
    return dispatch => {

        Axios.get(`${PROXY}${API_URL}${changeTaskStatusEndpoint(taskStatus)}/${oid}`)
            .then(response => {
                console.log("changeTaskStatus -> response", response.data)
                switch (response.data) {
                    case 1:
                        dispatch({
                            type: UPDATE_TASK,
                            payload: { oid, estado: 1 }
                        })
                        message.warning(<p>El estado de la tarea a pasado a <strong>EN CURSO</strong></p>)
                        break;
                    case 3:
                        dispatch({
                            type: UPDATE_TASK,
                            payload: { oid, estado: 3 }
                        })
                        message.success("Tarea finalizada correctamente.")
                        break;

                    default:
                        break;
                }
            }).catch(error => console.log(error))
    }
}