import React, { Fragment } from 'react';
import { Badge, Avatar, Popover } from 'antd';
import { UserOutlined } from '@ant-design/icons';

const MockedNotifications = [
    { 'type': 'normal', 'content': 'Notification 1' },
    { 'type': 'normal', 'content': 'Notification 2' },
    { 'type': 'normal', 'content': 'Notification 3' },
    { 'type': 'normal', 'content': 'Notification 4' },
];

const renderNotifications = (notifications) => {
    let nots = [];
    notifications.map((notification, index) =>
        nots.push(
            <p key={`notification-${index}`}>
                {notification.type} - {notification.content}
            </p>)
    );
    return nots;
};


const AppUserNotifications = () => {

    return (
        <Fragment>
            <div className="user_toolbar px-3">
                <Popover content={renderNotifications(MockedNotifications)} title="Notificaciones">
                    <Badge count={8}>
                        <Avatar icon={<UserOutlined />} />
                    </Badge>
                </Popover>
            </div>
        </Fragment>
    );
};

export default AppUserNotifications;