import React, { useState, Fragment } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Drawer, Menu, Dropdown, Button, } from 'antd';
import { UserOutlined, CloseCircleOutlined } from '@ant-design/icons';
import Text from 'antd/lib/typography/Text';
import { AUTHORIZATION, APP_COOKIE_NAME } from '../../constants';
import { setCookie } from '../../utils';
import AppUserNotifications from './AppUserNotifications';

import './../css/AppUser.css';

const AppUser = () => {

    const dispatch = useDispatch();
    const userInfo = useSelector(state => state.user_info)

    const [showUserInfo, setShowUserInfo] = useState(false);

    const logOut = () => {
        setCookie(APP_COOKIE_NAME, '');
        dispatch({ type: AUTHORIZATION, payload: { token: '', auth: false } });
    };

    const userMenu = (
        <Menu>
            <Menu.Item>
                <Button onClick={() => setShowUserInfo(!showUserInfo)} type="link" icon={<UserOutlined />} className="text-decoration-none text-left" href="#">
                    VER PERFIL
                </Button>
            </Menu.Item>
            <Menu.Item>
                <Button onClick={logOut} type="link" icon={<CloseCircleOutlined />} className="text-decoration-none text-left" href="#">
                    CERRAR SESIÓN
                </Button>
            </Menu.Item>
        </Menu>
    );

    return (
        <Fragment>

            <div className="px-4 d-flex">
                <AppUserNotifications />
                <Dropdown overlay={userMenu} className="text-left user_toolbar px-2">
                    <Text className="mr-2">{userInfo.nombre}</Text>
                </Dropdown>
            </div>

            <Drawer visible={showUserInfo} width="100%" closable onClose={() => setShowUserInfo(false)}>
                <div>
                    USER INFO DRAWER
                </div>
            </Drawer>

        </Fragment>
    );
};

export default AppUser;