import React, { Fragment } from 'react';
import { Tag } from 'antd';
import AppIcon from '../layouts/components/AppIcon';

const levels = [
    { icon: "chevron-double-down", color: "#ff874d", label: "Baja" },
    { icon: "grip-lines", color: "#52c41a", label: "Media" },
    { icon: "chevron-double-up", color: "#d9363e", label: "Alta" },
]

const PriorityText = ({ level, tooltip, title }) => {
    return (
        <Fragment>
            {title ? <strong>{title}</strong> : ''}
            <Tag color={levels[level].color} className="ml-3">
                <AppIcon i={`fa-${levels[level].icon} mr-2`} t="fad" />
                {levels[level].label}
            </Tag>
        </Fragment>
    )
}

export default PriorityText;