import React, { Fragment } from 'react';

const AppIcon = ({ i, t, className }) => {
    return (
        <Fragment>
            {i
                ? <span><i className={`${t ? t : 'fal'} ${i} app-icon ${className ? className : ''}`} /></span>
                : <span>404 icon.</span>}
        </Fragment>
    );
}

export default AppIcon;