import React, { useState, useEffect } from 'react';
import { Button, Modal } from 'antd';
import AppIcon from '../../layouts/components/AppIcon';
import { ReactMic } from 'react-mic';
import WaveSurfer from 'wavesurfer.js';
import './css/RecordAudioTool.css';
import FadeIn from 'react-fade-in';

const RecordAudioTool = () => {

    const [wavesurfer, setWavesufer] = useState()
    const [show, setShow] = useState(false);
    const [recording, setRecord] = useState(false);
    const [recorded, setRecorded] = useState(false);
    const [audio, setAudio] = useState();

    const recordHandler = e => {
        setWavesufer(WaveSurfer.create({
            container: '#waveform',
            waveColor: '#0490E2',
            progressColor: '#FFF'
        }))
        setAudio(e.blobURL);
        setRecorded(true);
    }

    const startRecording = () => {
        document.getElementById('waveform').innerHTML = null;
        setRecord(true);
        setRecorded(false);
    }

    const playPause = () => {
        wavesurfer.playPause();
    }

    useEffect(() => {
        if (wavesurfer && audio) {
            wavesurfer.load(audio)
        }
    })



    return (
        <div>
            <Button onClick={() => setShow(!show)} icon={<AppIcon i="fa-microphone-alt" t="fad" />} size="large" />

            <Modal title="Grabar audio" visible={show} onOk={() => setShow(false)}>
                {
                    !recorded
                        ? <ReactMic record={recording} className="sound-wave" strokeColor="#0490E2" backgroundColor="#333"
                            onStop={(e) => recordHandler(e)} /* onData={(e) => console.log(e)} */ />

                        : null
                }

                <div id="waveform"></div>

                {
                    recorded
                        ? <FadeIn><div className="mt-2 d-flex justify-content-center">
                            <Button className="mr-4" onClick={() => playPause()} icon={<AppIcon i="fa-play" t="fad" />} />
                            <Button className="ml-4" onClick={() => playPause()} icon={<AppIcon i="fa-pause" t="fad" />} />
                        </div></FadeIn> : null
                }

                <div className="d-flex justify-content-around mt-4">
                    {
                        !recording
                            ? <Button size="large" block icon={<AppIcon i="fa-record-vinyl" t="fad" />}
                                onClick={() => startRecording()} >
                                Iniciar grabación
                                </Button>
                            : <Button size="large" block className={`${recording ? 'recording' : ''}`} icon={<AppIcon i="fa-stop" t="fad" />}
                                onClick={() => setRecord(false)}>
                                Detener grabación
                                </Button>
                    }
                </div>
            </Modal>
        </div>
    )
}

export default RecordAudioTool;