import React from 'react';
import logo_basic from './../../assets/img/logo.png'
import logo_color from './../../assets/img/logo_color.png';
import logo_iso from './../../assets/img/logo_iso.png';

const AppLogo = ({ type, width, pos }) => {
    switch (type) {
        case "basic":
            return  <div className={`${pos ? 'text-' + pos : 'text-center'}`}>
                        <img width={width ? width : '40%'} src={logo_basic} alt="Logo" />
                    </div>
        case "color":
            return  <div className={`${pos ? 'text-' + pos : 'text-center'}`}>
                        <img width={width ? width : '40%'} src={logo_color} alt="Logo" />
                    </div>
        case "iso":
            return  <div className={`${pos ? 'text-' + pos : 'text-center'}`}>
                        <img width={width ? width : '40%'} src={logo_iso} alt="Logo" />
                    </div>
        default:
            return  <div className={`${pos ? 'text-' + pos : 'text-center'}`}>
                        <img width={width ? width : '40%'} src={logo_basic} alt="Logo" />
                    </div>

    }
}

export default AppLogo;