import React from 'react';
import { Form, Input, Button, Checkbox, Layout, Divider } from 'antd';
import { UserOutlined, LockOutlined, LoginOutlined } from '@ant-design/icons';
import { Row, Col } from 'react-flexbox-grid';
import AppFooter from './AppFooter';
import AppLogo from './AppLogo';
import { getCookie, setCookie } from '../../utils';

const AppLoginForm = ({ getLoginData }) => {

    const username = getCookie('kuara-login-credentials-usr')
    const password = getCookie('kuara-login-credentials-pwd')

    const onFinish = credentials => {
        getLoginData(credentials);
        if (credentials.remember) {
            setCookie('kuara-login-credentials-usr', credentials.username)
            setCookie('kuara-login-credentials-pwd', credentials.password)
        } else {
            setCookie('kuara-login-credentials-usr', '')
            setCookie('kuara-login-credentials-pwd', '')
        }
    }

    return (
        <Layout style={{ height: '100vh' }}>

            <Row className="m-auto">
                <Col xs={8} md={4} lg={2} className="m-auto">
                    <AppLogo type="color" width="50%" />
                    <Divider dashed style={{ borderColor: '#292a5c' }} />
                    <Form autoComplete="true" initialValues={{ remember: true, username, password }} layout="vertical" onFinish={onFinish}>
                        <Form.Item name="username" rules={[{ required: true, message: '¡Campo obligatorio!', },]}>
                            <Input placeholder="Usuario" prefix={<UserOutlined />} />
                        </Form.Item>

                        <Form.Item name="password" rules={[{ required: true, message: '¡Campo obligatorio!', },]}>
                            <Input.Password placeholder="Contraseña" prefix={<LockOutlined />} />
                        </Form.Item>

                        <Form.Item name="remember" valuePropName="checked">
                            <Checkbox>Recordar credenciales</Checkbox>
                        </Form.Item>

                        <Button block type="primary" htmlType="submit" icon={<LoginOutlined />}>
                            ENTRAR
                        </Button>
                    </Form>
                </Col>
            </Row>

            <AppFooter />

        </Layout>
    )
}

export default AppLoginForm;