import React, { useState } from 'react';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import AppIcon from './AppIcon';
import history from './../../history'
import './../css/AppMenu.css';

const AppMenu = ({ handleSelected }) => {

    const [value, setValue] = useState(history.location.pathname);

    const handleChange = (event, newValue) => {
        setValue(newValue);
        history.push(newValue);
        handleSelected(newValue);
    };

    return (
        <BottomNavigation value={value} onChange={handleChange} className="fixed-bottom">
            <BottomNavigationAction label="INICIO" value="/" icon={<AppIcon i="fa-home" t="fad" />} />
            <BottomNavigationAction label="PROPIAS" value="/tasks/own" icon={<AppIcon i="fa-user-clock" t="fad" />} />
            <BottomNavigationAction label="SUPERVISADAS" value="/tasks/supervised" icon={<AppIcon i="fa-clipboard-user" t="fad" />} />
            <BottomNavigationAction label="FINALIZADAS" value="/tasks/ended" icon={<AppIcon i="fa-user-check" t="fad" />} />
        </BottomNavigation>
    );
}

export default AppMenu;