import React, { Fragment } from 'react';
import { Row, Col } from 'react-flexbox-grid';
import { Empty } from 'antd';
import AppTitle from '../layouts/components/AppTitle';
import AppIcon from './../layouts/components/AppIcon';
import Task from '../components/Task';

const Tasks = ({ data, type }) => {

    /* RENDER TAREAS */
    const renderTasks = () => {
        console.time('Tiempo renderizado tareas');
        const output = [];
        if (data.length === 0 || data[0] === null) {
            return <Col xs={12} md={12} lg={12} className="text-center"><Empty description="No hay tareas para mostrar." /></Col>
        } else {
            for (let t in data) {
                if (data[t]) {
                    output.push(
                        <Task data={data[t]} type={type} key={`tasks-${t}`} />
                    )
                }
            }
            console.timeEnd('Tiempo renderizado tareas')
            return output;
        }
    }

    return (
        <Fragment>
            <AppTitle title="TAREAS" icon={<AppIcon t="fad" i="fa-tasks-alt" />} />

            <Row className="mb-2" start="xs">
                <Col xs={12} md={12} lg={12} >

                </Col>
            </Row>

            <Row className="mb-2" start="xs">
                <Col xs={12} md={12} lg={12} >
                    <small>{data[0] !== null ? data.length : 0} Tarea(s) listadas.</small>
                </Col>
            </Row>

            <Row>
                {renderTasks()}
            </Row>
        </Fragment>
    )
}

export default Tasks;