import React, { Fragment } from 'react';
import moment from 'moment';
import 'moment/locale/es'
import { Typography, Spin, Progress, Divider } from 'antd';
import FadeIn from 'react-fade-in';
import AppIcon from '../layouts/components/AppIcon';
import { PROGRESS_STROKE_COLOR } from './../constants';
import PriorityText from '../components/PriorityText';
import TaskToolBar from '../components/TaskToolBar/';

import './css/Tasks.css';

const { Title } = Typography;
const divider_style = { borderColor: '#4f2a693d', margin: '15px 0px' };

const TaskExtended = ({ data }) => {
    if (data) {
        /* CONFIGURACIÓN DE FECHAS */
        var now = moment();
        var start = moment(data.fechaInicio);
        var end = moment(data.fechaVencimiento);
        const taskPercent = () => {
            start = start || moment(now).startOf('day');
            end = end || moment(now).endOf('day');
            let totalMillisInRange = end.valueOf() - start.valueOf();
            let elapsedMillis = now.valueOf() - start.valueOf();
            return Math.round(Math.max(0, Math.min(100, 100 * (elapsedMillis / totalMillisInRange))));
        }
        return (
            <Fragment>
                <FadeIn>
                    <div className="task-extended">
                        <Title strong level={4} className="m-0">{data.descripcion}</Title>

                        <Progress steps={19} percent={taskPercent()} strokeColor={PROGRESS_STROKE_COLOR.to} className="mb-3" />

                        <p> <AppIcon i="fa-calendar-check" t="fal" /> Fecha de inicio: <strong>{start.format('L')}</strong></p>
                        <p> <AppIcon i="fa-calendar-exclamation" t="fal" /> Fecha de vencimiento: <strong>{end.format('L')}</strong></p>
                        <p> <AppIcon i="fa-alarm-exclamation" t="fal" /> Hora: <strong>{data.horaVencimiento}</strong></p>

                        <Divider dashed style={divider_style} />

                        <p> <AppIcon i="fa-clipboard-user" f="fal" /> Responsable: <strong>{data.primerResponsableNombre}</strong></p>
                        <p> <AppIcon i="fa-user-tie" f="fal" /> Supervisor: <strong>{data.supervisorNombre}</strong></p>
                        <p> <AppIcon i="fa-project-diagram" f="fal" /> Proyecto: <strong>{data.proyectoNombre}</strong></p>

                        <Divider dashed style={divider_style} />

                        <PriorityText level={data.prioridad} title="PRIORIDAD" tooltip/>

                        <Divider dashed style={divider_style} />

                        <p> <AppIcon i="fa-comments-alt" f="fal" /> Observaciones: <strong>{data.observaciones}</strong></p>

                        <TaskToolBar taskID={data.oid} />

                    </div>
                </FadeIn>
            </Fragment>
        )
    } else {
        return (<Spin tip="Cargando tareas..." className="w-100 m-auto" />)
    }
}

export default TaskExtended;