import { PROXY, API_URL, LOGIN_ENDPOINT, GET_ALL_USERS } from './../constants/api';
import { AUTHORIZATION, APP_COOKIE_NAME } from '../constants';
import { setCookie } from '../utils';
import { find } from 'lodash';
import Axios from 'axios';

export const login = credentials => {
    return dispatch => {
        // USER OID
        let oid = '';

        //GET ALL USERS AND CHECK USERNAME
        Axios.get(`${PROXY}${API_URL}${GET_ALL_USERS}`)
            .then(response => {
                const current_user = find(response.data, ['userName', credentials.username]);
                if (current_user.isActive) {
                    oid = current_user.oid;
                    Axios.get(`${PROXY}${API_URL}${LOGIN_ENDPOINT}${oid}/${credentials.password}`)
                        .then(isUserAuthorized => {
                            if (isUserAuthorized) {
                                dispatch({
                                    type: AUTHORIZATION,
                                    payload: { token: oid, auth: true }
                                });
                                setCookie(APP_COOKIE_NAME, oid, 1);
                            }
                        }
                    )
                }
            }
            ).catch(error => {
                console.log(error)
            }
        )
    }
}