import React from 'react';
import { Button } from 'antd';
import AppIcon from '../../layouts/components/AppIcon';

const AddFileTool = () => {
    const addFile = () => {
        let input = document.getElementById('fileInput');
        input.click();
    }
    return (
        <div>
            <input type="file" id="fileInput" className="d-none" />
            <Button onClick={() => addFile()} icon={<AppIcon i="fa-camera-retro" t="fad" />} size="large" />
        </div>
    )
}

export default AddFileTool;